<template>
	<view class="video-page">
		<view class="sub-menu-list">
			<p @click="curSubMenu=it.value" :class="['sub-menu-name',{active:curSubMenu==it.value}]"
				v-for="it in subMenuList" :key="it.value">{{it.label}}
			</p>
		</view>
		<swiper class="sub-menu-con-swiper" :current="curSubMenu" duration="300" @change="subMenuContChange">
			<swiper-item class="sub-menu-con-swiper-item">
				<view class="menu-content">


				</view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				subMenuList: [{
					label: '视频',
					value: 'shipin'
				}, {
					label: 'MV',
					value: 'mv'
				}],
				curSubMenu: 'shipin',
			}
		},
		methods: {
			// 切换子菜单内容
			subMenuContChange(e) {
				console.log('e', e)
			}
		}
	}
</script>

<style lang="scss">
	.video-page {
		width: 100%;
		height: 100%;

		.sub-menu-list {
			width: 100%;
			height: 60rpx;
			display: flex;
			align-items: center;
			padding: 0 20rpx;
			box-sizing: border-box;
			background: pink;

			.sub-menu-name {
				width: 110rpx;
				font-size: 26rpx;
				color: #a8a8a8;
				position: relative;
				background-color: skyblue;

				&:after {
					content: '';
					display: block;
					width: 1rpx;
					height: 20rpx;
					background: #2b2b2b;
					position: absolute;
					right: 30rpx;
					top: -8rpx;
				}

				&:last-of-type:after {
					display: none;
				}

				&.active {
					color: #fdfdfd;
				}
			}
		}
		.sub-menu-con-swiper {
			width: 100%;
			height: 100%;
			&-item {
				width: 100%;
				height: 100%;
				background-color: skyblue;
				.menu-content {
					background-color: pink;
				}
			}
		}
	}
</style>
